"use client";

import { useEffect, useState, useMemo } from "react";
import { useTeams } from "@/features/teams/lib/use-teams";
import { Team, TeamMember } from "@/entities/team/model/types";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
  CardFooter,
} from "@/shared/ui/card";
import { Button } from "@/shared/ui/button";
import { Input } from "@/shared/ui/input";
import { Label } from "@/shared/ui/label";
import { useToast } from "@/shared/ui/use-toast";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/shared/ui/dialog";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/shared/ui/tabs";
import { User, Users, Trash2 } from "lucide-react";
import { useAuthContext } from "@/app/providers/auth-provider";
import { Avatar, AvatarFallback } from "@/shared/ui/avatar";
import { useRouter } from "next/navigation";

export default function TeamsPage() {
  const router = useRouter();
  const {
    isLoading,
    teams,
    createTeam,
    joinTeam,
    fetchTeams,
    getTeamMembers,
    deleteTeam,
  } = useTeams();
  const { toast } = useToast();
  const { user } = useAuthContext();
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isJoinDialogOpen, setIsJoinDialogOpen] = useState(false);
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [teamToDelete, setTeamToDelete] = useState<number | null>(null);
  const [newTeamName, setNewTeamName] = useState("");
  const [joinCode, setJoinCode] = useState("");
  const [teamMembers, setTeamMembers] = useState<{
    [key: number]: TeamMember[];
  }>({});
  const [selectedTeam, setSelectedTeam] = useState<number | null>(null);

  // Check if user is a student
  useEffect(() => {
    if (!user?.roles) return;

    const isStudent = user.roles.some(
      (role) => role.toLowerCase() === "student"
    );

    if (!isStudent) {
      toast({
        title: "Доступ запрещен",
        description: "Эта страница доступна только для студентов",
        variant: "destructive",
      });
      router.push("/dashboard");
      return;
    }

    fetchTeams();
  }, [user, router, toast, fetchTeams]);

  const myTeams = useMemo(() => {
    if (!user?.id || !teams) return [];
    return teams.filter((team) =>
      team.members.some((member) => Number(member.id) === Number(user.id))
    );
  }, [teams, user?.id]);

  const handleShowMembers = async (teamId: number) => {
    if (selectedTeam === teamId) {
      setSelectedTeam(null);
      return;
    }

    try {
      const members = await getTeamMembers(teamId);
      setTeamMembers((prev) => ({ ...prev, [teamId]: members }));
      setSelectedTeam(teamId);
    } catch (error) {
      console.error("Error fetching team members:", error);
      toast({
        title: "Ошибка",
        description: "Не удалось загрузить список участников",
        variant: "destructive",
      });
    }
  };

  const handleDeleteTeam = (teamId: number) => {
    setTeamToDelete(teamId);
    setIsDeleteDialogOpen(true);
  };

  const handleCreateTeam = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!newTeamName.trim()) {
      toast({
        title: "Ошибка",
        description: "Введите название команды",
        variant: "destructive",
      });
      return;
    }

    try {
      await createTeam(newTeamName);
      setIsCreateDialogOpen(false);
      setNewTeamName("");
      // Refresh teams list after creation
      await fetchTeams();
      toast({
        title: "Успешно",
        description: "Команда успешно создана",
      });
    } catch (error) {
      console.error("Error creating team:", error);
      toast({
        title: "Ошибка",
        description: "Не удалось создать команду",
        variant: "destructive",
      });
    }
  };

  const handleJoinTeam = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!joinCode.trim()) {
      toast({
        title: "Ошибка",
        description: "Введите код команды",
        variant: "destructive",
      });
      return;
    }

    try {
      await joinTeam(joinCode);
      setIsJoinDialogOpen(false);
      setJoinCode("");
      // Refresh teams list after joining
      await fetchTeams();
      toast({
        title: "Успешно",
        description: "Вы присоединились к команде",
      });
    } catch (error) {
      console.error("Error joining team:", error);
      toast({
        title: "Ошибка",
        description: "Не удалось присоединиться к команде",
        variant: "destructive",
      });
    }
  };

  const handleConfirmDelete = async () => {
    if (!teamToDelete) return;

    try {
      await deleteTeam(teamToDelete);
      setIsDeleteDialogOpen(false);
      setTeamToDelete(null);
      // Refresh teams list after deletion
      await fetchTeams();
      toast({
        title: "Успешно",
        description: "Команда удалена",
      });
    } catch (error) {
      console.error("Error deleting team:", error);
      toast({
        title: "Ошибка",
        description: "Не удалось удалить команду",
        variant: "destructive",
      });
    }
  };

  if (isLoading) {
    return <div>Загрузка...</div>;
  }

  return (
    <div className="container mx-auto py-6">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Мои команды</h1>
        <div className="space-x-2">
          <Button onClick={() => setIsJoinDialogOpen(true)}>
            Присоединиться к команде
          </Button>
          <Button onClick={() => setIsCreateDialogOpen(true)}>
            Создать команду
          </Button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {myTeams.length > 0 ? (
          myTeams.map((team) => (
            <Card key={team.id}>
              <CardHeader>
                <CardTitle className="flex justify-between items-center">
                  <span>{team.name}</span>
                  {Number(team.creator_id) === Number(user?.id) && (
                    <Button
                      variant="ghost"
                      size="icon"
                      onClick={() => handleDeleteTeam(team.id)}
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  )}
                </CardTitle>
                <CardDescription>Код команды: {team.code}</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex items-center gap-2">
                  <Users className="h-4 w-4" />
                  <span>
                    {team.members.length}{" "}
                    {team.members.length === 1 ? "участник" : "участников"}
                  </span>
                </div>
                <div className="flex items-center gap-2 mt-2">
                  <User className="h-4 w-4" />
                  <span>
                    Роль:{" "}
                    {Number(team.creator_id) === Number(user?.id)
                      ? "Создатель"
                      : "Участник"}
                  </span>
                </div>
              </CardContent>
              <CardFooter>
                <Button
                  variant="outline"
                  className="w-full"
                  onClick={() => handleShowMembers(team.id)}
                >
                  {selectedTeam === team.id
                    ? "Скрыть участников"
                    : "Показать участников"}
                </Button>
                {selectedTeam === team.id && teamMembers[team.id] && (
                  <div className="mt-4 space-y-2">
                    {teamMembers[team.id].map((member) => (
                      <div
                        key={member.id}
                        className="flex items-center gap-2 p-2 rounded-lg bg-gray-50"
                      >
                        <Avatar className="h-8 w-8">
                          <AvatarFallback>
                            {member.fullname
                              .split(" ")
                              .map((n) => n[0])
                              .join("")
                              .toUpperCase()}
                          </AvatarFallback>
                        </Avatar>
                        <div className="flex-1 min-w-0">
                          <p className="text-sm font-medium truncate">
                            {member.fullname}
                          </p>
                        </div>
                        {Number(team.creator_id) === Number(member.id) && (
                          <span className="text-xs text-gray-500">
                            Создатель
                          </span>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </CardFooter>
            </Card>
          ))
        ) : (
          <div className="col-span-full text-center text-gray-500">
            <p>Вы пока не состоите ни в одной команде</p>
          </div>
        )}
      </div>

      {/* Create Team Dialog */}
      <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Создать команду</DialogTitle>
            <DialogDescription>
              Введите название для новой команды
            </DialogDescription>
          </DialogHeader>
          <form onSubmit={handleCreateTeam}>
            <div className="grid gap-4 py-4">
              <div className="grid gap-2">
                <Label htmlFor="name">Название команды</Label>
                <Input
                  id="name"
                  value={newTeamName}
                  onChange={(e) => setNewTeamName(e.target.value)}
                  placeholder="Введите название команды"
                />
              </div>
            </div>
            <DialogFooter>
              <Button type="submit">Создать</Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>

      {/* Join Team Dialog */}
      <Dialog open={isJoinDialogOpen} onOpenChange={setIsJoinDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Присоединиться к команде</DialogTitle>
            <DialogDescription>
              Введите код команды для присоединения
            </DialogDescription>
          </DialogHeader>
          <form onSubmit={handleJoinTeam}>
            <div className="grid gap-4 py-4">
              <div className="grid gap-2">
                <Label htmlFor="code">Код команды</Label>
                <Input
                  id="code"
                  value={joinCode}
                  onChange={(e) => setJoinCode(e.target.value)}
                  placeholder="Введите код команды"
                />
              </div>
            </div>
            <DialogFooter>
              <Button type="submit">Присоединиться</Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>

      {/* Delete Team Dialog */}
      <Dialog open={isDeleteDialogOpen} onOpenChange={setIsDeleteDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Удалить команду</DialogTitle>
            <DialogDescription>
              Вы уверены, что хотите удалить команду? Это действие нельзя
              отменить.
            </DialogDescription>
          </DialogHeader>
          <DialogFooter>
            <Button
              variant="outline"
              onClick={() => setIsDeleteDialogOpen(false)}
            >
              Отмена
            </Button>
            <Button variant="destructive" onClick={handleConfirmDelete}>
              Удалить
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
